import { Contract } from "@ethersproject/contracts";
import { Web3Provider } from "@ethersproject/providers";
import { useWeb3React } from "@web3-react/core";
import { InputHTMLAttributes, useCallback, useEffect, useMemo, useState } from "react";
import { useLocation, useParams } from "react-router";
import { networksByChainId, setNetwork } from "../utils/EthereumUtils";
import { TokenContract } from "./TokenContract";

const abi = [
  {
    inputs: [
      {
        internalType: "address",
        name: "account",
        type: "address",
      },
    ],
    name: "balanceOf",
    outputs: [
      {
        internalType: "uint256",
        name: "",
        type: "uint256",
      },
    ],
    stateMutability: "view",
    type: "function",
  },
  {
    inputs: [
      {
        internalType: "address",
        name: "tokenAddress",
        type: "address",
      },
      {
        internalType: "uint256",
        name: "tokenAmount",
        type: "uint256",
      },
    ],
    name: "recoverERC20",
    outputs: [],
    stateMutability: "nonpayable",
    type: "function",
  },
  {
    inputs: [],
    name: "totalSupply",
    outputs: [
      {
        internalType: "uint256",
        name: "",
        type: "uint256",
      },
    ],
    stateMutability: "view",
    type: "function",
  },
  {
    inputs: [
      {
        internalType: "address",
        name: "recipient",
        type: "address",
      },
      {
        internalType: "uint256",
        name: "amount",
        type: "uint256",
      },
    ],
    name: "transfer",
    outputs: [
      {
        internalType: "bool",
        name: "",
        type: "bool",
      },
    ],
    stateMutability: "nonpayable",
    type: "function",
  },
];

const useQuery = () => new URLSearchParams(useLocation().search);

export default function Tokens() {
  const { address } = useParams<{ address: string }>();
  const query = useQuery();
  const urlChainId = Number(query.get("chainId"));
  const { library, account, chainId } = useWeb3React<Web3Provider>();
  if (!library || !account || !chainId) {
    throw new Error();
  }
  const contract = useMemo(
    () => new Contract(address, abi, library.getSigner()) as TokenContract,
    [address, library]
  );
  return urlChainId === chainId ? (
    <>
      <h1>Token at {address}</h1>
      <BalanceOf contract={contract} />
      <RecoverERC20 contract={contract} />
      <TotalSupply contract={contract} />
      <Transfer contract={contract} />
    </>
  ) : (
    <button onClick={() => setNetwork(library, networksByChainId[`0x${urlChainId.toString(16)}`])}>
      Switch to {urlChainId}
    </button>
  );
}

function BalanceOf({ contract }: { contract: TokenContract }) {
  const [balance, setBalance] = useState<
    { t: "empty" | "loading" } | { t: "loaded"; value: string }
  >({ t: "empty" });
  const [account, setAccount] = useState("");
  return (
    <>
      <input
        placeholder="account"
        value={account}
        onChange={({ target: { value } }) => setAccount(value)}
      />
      <button
        onClick={() => {
          setBalance({ t: "loading" });
          contract.balanceOf(account).then(
            r => setBalance({ t: "loaded", value: r.toString() }),
            e => {
              setBalance({ t: "empty" });
              throw e;
            }
          );
        }}
        disabled={balance.t === "loading"}
      >
        balanceOf
      </button>
      <div>
        {balance.t === "loading" && "Loading..."}
        {balance.t === "loaded" && balance.value}
      </div>
    </>
  );
}

function RecoverERC20({ contract }: { contract: TokenContract }) {
  const [loading, setLoading] = useState(false);
  const [tokenAddress, setTokenAddress] = useState("");
  const [tokenAmount, setTokenAmount] = useState("");
  return (
    <>
      <input
        placeholder="tokenAddress"
        value={tokenAddress}
        onChange={({ target: { value } }) => setTokenAddress(value)}
      />
      <input
        placeholder="tokenAmount"
        value={tokenAmount}
        onChange={({ target: { value } }) => setTokenAmount(value)}
      />
      <button
        onClick={() => {
          setLoading(true);
          contract.recoverERC20(tokenAddress, tokenAmount).finally(() => setLoading(false));
        }}
        disabled={loading}
      >
        recoverERC20
      </button>
      <div>{loading && "Loading..."}</div>
    </>
  );
}

function TotalSupply({ contract }: { contract: TokenContract }) {
  const [totalSupply, setTotalSupply] = useState<
    { t: "error" | "loading" } | { t: "loaded"; value: string }
  >({ t: "loading" });
  useEffect(() => {
    contract.totalSupply().then(
      r => setTotalSupply({ t: "loaded", value: r.toString() }),
      e => {
        setTotalSupply({ t: "error" });
        throw e;
      }
    );
  }, [contract]);
  return (
    <>
      Total supply:{" "}
      {(() => {
        switch (totalSupply.t) {
          case "error":
            return "Couldn't load";
          case "loading":
            return "Loading...";
          case "loaded":
            return totalSupply.value;
        }
      })()}
    </>
  );
}

function useInput() {
  const [value, setValue] = useState("");
  const onChange: InputHTMLAttributes<HTMLInputElement>["onChange"] = useCallback(
    ({ target: { value } }) => setValue(value),
    [setValue]
  );
  return { value, onChange };
}

function Transfer({ contract }: { contract: TokenContract }) {
  const recipient = useInput();
  const amount = useInput();
  return (
    <div>
      <input placeholder="recipient" {...recipient} />
      <input placeholder="amount" {...amount} />
      <button onClick={() => contract.transfer(recipient.value, amount.value)}>transfer</button>
    </div>
  );
}
