import { useHistory } from "react-router-dom";
import { StoredToken } from "../token_creator/connected";

export default function Tokens() {
  return (
    <>
      <h1>Tokens</h1>
      <TokenTable />
    </>
  );
}
function TokenTable() {
  const history = useHistory();
  const tokens = JSON.parse(window.localStorage.getItem("tokens") ?? "[]") as StoredToken[];
  return (
    <table>
      <thead>
        <tr>
          <th>Address</th>
          <th>Type</th>
          <th>Symbol</th>
          <th>Decimals</th>
          <th>ChainID</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        {tokens.map(t => (
          <tr key={t.address}>
            <td>{t.address}</td>
            <td>{t.type}</td>
            <td>{t.symbol}</td>
            <td>{t.decimals}</td>
            <td>{t.chainId}</td>
            <td>
              <button onClick={() => history.push(`/tokens/${t.address}?chainId=${t.chainId}`)}>
                Details
              </button>
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}
